/*
* WooCommerce Fluid Checkout - Progress bar: Default
 */

#wfc-progressbar {
	margin: 0;
	overflow: hidden;
}
.wfc-progress-bar-step {
	display: inline-block;
	padding: .5rem 2rem;
	box-sizing: border-box;
	position: relative;
	cursor: pointer;
}
@supports ( display: flex ) {
	#wfc-progressbar {
		display: flex;
	}
	.wfc-progress-bar-step {
		flex: 1;
		padding: 0;
	}
}

.wfc-progress-bar-step[disabled],
.wfc-progress-bar-step.disabled {
	cursor: default;
}

.wfc-progress-bar-step:first-child:before,
.wfc-progress-bar-step:last-child:after {
	content: '';
	display: none;
}




#wfc-progressbar {
	border-radius: 4px;
	margin-bottom: 30px;
}



.wfc-progress-bar-step {
	padding: 5px;
	max-height: 30px;
	height: 30px;
	background-color: #f3f3f3;
	font-size: 15px;
	text-align: center;
	line-height: 20px;
}

.wfc-progress-bar-step:before,
.wfc-progress-bar-step:after {
	content: '';
	position: absolute;
	top: -2px;
	right: -23px;
	width: 0;
	height: 0;
	border: 17px solid transparent;
	border-left: 7px solid #f3f3f3;
	z-index: 3;
}

.wfc-progress-bar-step:before {
	left: 0;
	border: 17px solid transparent;
	border-left: 7px solid #fff;
	z-index: 2;
}



.wfc-progress-bar-step.current:after {
	border-left-color: #e9e9e9;
}
.wfc-progress-bar-step.current {
	background-color: #e9e9e9;
}



.wfc-progress-bar-step.done:after {
	border-left-color: #329f5b;
}
.wfc-progress-bar-step.done {
	color: #fff;
	background-color: #329f5b;
}
